import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import CssBaseline from '@mui/material/CssBaseline';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Divider from '@mui/material/Divider';
import { Drawer, ListItem, Paper } from '@mui/material';
import Box from '@mui/material/Box';
import GitHubIcon from '@mui/icons-material/GitHub';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import FiberManualRecordRoundedIcon from '@mui/icons-material/FiberManualRecordRounded';
import MenuIcon from '@mui/icons-material/Menu';
import { IconButton } from '@mui/material';
import { styled, useTheme } from '@mui/material/styles';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import MuiAppBar from '@mui/material/AppBar';
import List from '@mui/material/List';

const drawerWidth = 300;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
      flexGrow: 1,
      padding: theme.spacing(3),
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      marginLeft: `-${drawerWidth}px`,
      ...(open && {
        transition: theme.transitions.create('margin', {
          easing: theme.transitions.easing.easeOut,
          duration: theme.transitions.duration.enteringScreen,
        }),
        marginLeft: 10,
      }),
    }),
  );

  const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
  })(({ theme, open }) => ({
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: `${drawerWidth}px`,
      transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    }),
  }));

const defaultTheme = createTheme();

export const Layout = ({children}) => {
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
      };
    
      const handleDrawerClose = () => {
        setOpen(false);
      };
  return (
    <ThemeProvider theme={defaultTheme}>
        <Box sx={{display: 'flex'}}>
    <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }} open={open}>
        <Toolbar sx={{backgroundImage: 'url(../../blurBackground.jpeg)', width: '100%'}}>
        <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ mr: 2, ...(open && { display: 'none' }) }}
          >
            <MenuIcon />
          </IconButton>
            <Grid container spacing={0.2} sx={{ml: 5}}>
                <Grid item justifyContent={'center'} alignItems={'center'} display={'flex'}>
                <Typography variant="h6" noWrap component="div">
            Davila
          </Typography>
                </Grid>
                <Grid item justifyContent={'center'} alignItems={'center'} display={'flex'}>
                <Typography variant="h6" noWrap component="div" justifyContent={'center'} alignContent={'center'} display={'flex'}>
            <FiberManualRecordRoundedIcon sx={{fontSize:5}} />
          </Typography>
                </Grid>
                <Grid item justifyContent={'center'} alignItems={'center'} display={'flex'}>
                <Typography variant="h6" noWrap component="div">
            Corujo
          </Typography>
                </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
            width: drawerWidth,
            flexShrink: 0,
            '& .MuiDrawer-paper': {
                width: drawerWidth,
                boxSizing: 'border-box',
              },
        }}
        variant='persistent'
        anchor='left'
        open={open}
      >
        <Divider />
        <List
            sx={{
                        backgroundImage: 'url(../../blurBackground.jpeg)',
                        height: '100%'
                    }}>
        <ListItem key={'background'} style={{
            display: 'flex',
            alignItems: 'start',
            // necessary for content to be below app bar
            justifyContent: 'flex-end',
        }} sx={{mb: 5}}>
            <IconButton onClick={handleDrawerClose}>
                <ChevronLeftIcon />
            </IconButton>
        </ListItem>
            <ListItem key={'avatar'} style={{display:'flex', justifyContent: 'center'}} sx={{my: 5}}>
                <Avatar
                    alt='Andres Davila' 
                    src='../../andres.jpeg' 
                    sx={{ width: 100, height: 100, boxShadow: 1}}
                />
            </ListItem>
            <Divider />
            <ListItem key={'name'} style={{display:'flex', justifyContent: 'flex-end'}} sx={{my: 5}}>
                <Paper 
                    sx={{
                        backgroundColor: 'transparent'
                    }}
                    elevation={0}
                >
                    <Typography variant={"h4"} align='right' color={'white'} sx={{mr: 2}}>
                        Andres Davila
                    </Typography>
                    <Typography variant={"h6"} align='right' color={"gray"} sx={{mr: 2}}>
                        Software Engineer
                    </Typography>
                </Paper>
            </ListItem>
            <Divider />
            <ListItem key={'links'} style={{display:'flex', justifyContent: 'flex-end'}} sx={{my: 5}}>
                <Stack direction={"row"} spacing={2} display={"flex"} sx={{mr: 2}}>
                    <Link href='https://github.com/adavilacorujo' target='_blank' sx={{mt: '5rem'}}>
                    <GitHubIcon 
                        style={{
                            fontSize: '2rem',
                            color: 'gray'
                        }}
                    />
                    </Link>
                    <MailOutlineIcon
                        style={{
                            fontSize: '2rem',
                            color: 'gray'
                        }}
                    />
                </Stack>
            </ListItem>
        </List>
      </Drawer>
        </Box>
        <Box sx={{my: 10, mx: 3}}>
            {children}
        </Box>
    </ThemeProvider>
  );
}